@import url(//db.onlinewebfonts.com/c/066ce24dae3730ed6c648b09efaea93a?family=Acumin+Variable+Concept);
:root {
    --primary: #eeeeee;
  }
  
  .btn {
    padding: 8px 20px;
    border-radius: 2px;
    outline: none;
    border: none;
    cursor: pointer;
    font-family: 'Raleway', sans-serif, 'Acumin Variable Concept', Calibri, 'PT Sans', sans-serif;
    font-variation-settings: "wght" 240, "wdth" 100, "opsz" 33;
  }
  
  .btn--primary {
    background-color: #eeeeee;
    background-color: var(--primary);
    color: #242424;
    border: 1px solid #eeeeee;
    border: 1px solid var(--primary);
  }
  
  .btn--outline {
    background-color: transparent;
    color: #eeeeee;
    padding: 8px 20px;
    border: 1px solid #eeeeee;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;
  }
  
  .btn--medium {
    padding: 8px 20px;
    font-size: 18px;
  }
  
  .btn--large {
    padding: 12px 26px;
    font-size: 20px;
  }
  
  .btn--large:hover,
  .btn--medium:hover {
    transition: all 0.3s ease-out;
    background: #eeeeee;
    color: #242424;
    transition: 250ms;
  }
  
.navbar {
    /*background-color: #1A1E25;*/
    background: linear-gradient(90deg, rgb(26, 30, 37) 0%, rgb(17, 20, 25) 100%);
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 999;
  }
  
  .navbar-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80px;
    max-width: 1500px;
  }
  
  .navbar-logo {
    color: #EEEEEE;
    justify-self: start;
    margin-left: 20px;
    margin-bottom: -8px;
    cursor: pointer;
    text-decoration: none;
    font-size: 1.8rem;
    display: flex;
    align-items: center;
    font-family: 'Acumin Variable Concept', Calibri, 'PT Sans', sans-serif;
    font-variation-settings: "wght" 180, "wdth" 100, "opsz" 33;
  }

  .navbar-logo-white {
    cursor: pointer;
    width: 3.2rem;
    height: auto;
    margin-right: 7px;
    margin-bottom: 8px;
  }
  
  .fa-typo3 {
    margin-left: 0.5rem;
    font-size: 1.8rem;
  }
  
  .nav-menu {
    display: grid;
    grid-template-columns: repeat(6, auto);
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    width: 60vw;
    justify-content: end;
    margin-right: 1.5rem;
  }
  
  .nav-item {
    height: 80px;
  }
  
  .nav-links {
    color: #eeeeee;
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: .8rem 1rem 0.5rem 1rem;
    height: 100%;
    font-size: 1.2rem;
    font-family: 'Raleway', sans-serif, 'Acumin Variable Concept', Calibri, 'PT Sans', sans-serif;
    font-variation-settings: "wght" 200, "wdth" 100, "opsz" 33;
    text-transform: uppercase;
  }
  
  .nav-links:hover {
    border-bottom: 4px solid #eeeeee;
    transition: all 0.2s ease-out;
  }
  
  .fa-bars {
    color: #eeeeee;
  }
  
  .nav-links-mobile {
    display: none;
  }
  
  .menu-icon {
    display: none;
  }
  
  @media screen and (max-width: 960px) {
    .NavbarItems {
      position: relative;
    }
  
    .nav-menu {
      display: flex;
      flex-direction: column;
      width: 100%;
      height: 90vh;
      position: absolute;
      top: 80px;
      left: -100%;
      opacity: 1;
      transition: all 0.5s ease;
    }
  
    .nav-menu.active {
      background: #222831;
      left: 0;
      opacity: 1;
      transition: all 0.5s ease;
      z-index: 1;
    }
  
    .nav-links {
      text-align: center;
      padding: 2rem;
      padding-top: 2.4rem;
      width: 100%;
      display: table;
      font-variation-settings: "wght" 240, "wdth" 100, "opsz" 33;
    }
  
    .nav-links:hover {
      background-color: #eeeeee;
      color: #242424;
      border-radius: 0;
    }
  
    .navbar-logo {
      position: absolute;
      top: 4px;
      left: 0;
      transform: translate(25%, 50%);
    }
  
    .menu-icon {
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      transform: translate(-100%, 60%);
      font-size: 1.8rem;
      cursor: pointer;
    }
  
    .fa-times {
      color: #eeeeee;
      font-size: 2rem;
    }
  
    .nav-links-mobile {
      display: block;
      text-align: center;
      margin: 2rem auto;
      border-radius: 4px;
      width: 80%;
      text-decoration: none;
      font-size: 1.5rem;
      background-color: transparent;
      color: #eeeeee;
      padding: 14px 20px;
      border: 1px solid #fff;
      transition: all 0.3s ease-out;
    }
  
    .nav-links-mobile:hover {
      background: #eeeeee;
      color: #1A1E25;
      transition: 250ms;
    }
  }

