    overflow: hidden;
    text-decoration: none;
    cursor: pointer;
  }
  
  .s_cards__item__pic-wrap {
    position: relative;
    width: 100%;
    padding-top: 67%;
    overflow: hidden;
  }

  .s_cards__item__pic-wrap-direct {
    position: relative;
    width: 100%;
    padding-top: 57%;
