    background-color: #1A1E25;
    border: 2px solid #eeeeee;
    border-radius: 15px;
    width: 30px;
    height: 30px;
    color: #eeeeee;
    position: absolute;
    top: 0;
    right: 0;
    margin-top: -10px;
    margin-right: -10px;
    cursor: pointer;
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    padding-bottom: 5px;
    text-transform: uppercase;
    font-family: Arial, Calibri, Helvetica, sans-serif;
    font-size: 14px;
    box-shadow: 0 6px 20px rgba(0, 0, 0, 0.17);
