}

/* navigation */
.backdrop-nav-container {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
}

.backdrop-nav-left, .backdrop-nav-right {
    background-color: rgba(0, 0, 0, 0.0);
    position: absolute;
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 50px;
    height: 100%;
    opacity: .3;
    cursor: pointer;
}

.backdrop-nav-left {
    left: 0;
}

.backdrop-nav-right {
    right: 0;
}

.backdrop-nav-left:hover, .backdrop-nav-right:hover {
    background-color: rgba(0, 0, 0, 0.1);
    opacity: 1;
}

.backdrop-nav-left-icon, .backdrop-nav-right-icon {
    background-repeat: no-repeat;
    background-position: center;
    background-size: 70%;
    position: absolute;
    width: 50px;
    height: 50px;
}

.backdrop-nav-left-icon {
    background-image: url(data:image/png;base64,);
}

.backdrop-nav-right-icon {
    background-image: url(data:image/png;base64,);
}
