  }
  
  .cards__item__link {
    display: flex;
    flex-flow: column;
    width: 100%;
    /*box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);
    -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    border-radius: 10px;*/
    overflow: hidden;
    text-decoration: none;
    cursor: pointer;
  }
  
  .cards__item__pic-wrap {
    position: relative;
    width: 100%;
    padding-top: 67%;
    overflow: hidden;
  }
  
  .fade-img {
    -webkit-animation-name: fade-img;
            animation-name: fade-img;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
  }
  
  .cards__item__pic-wrap::after {
    content: attr(data-category);
    position: absolute;
    bottom: 0;
    margin-left: 10px;
    padding: 6px 8px;
    max-width: calc((100%) - 60px);
    font-size: 12px;
    font-weight: 700;
    color: #eeeeee;
    background-color: #d65a31;
    box-sizing: border-box;
  }

  .cards__item__image {
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    box-shadow: inset 0 0 0 1000px rgba(34,40,49,0.2);
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: block;
    object-fit: cover;
    transition: all 0.2s linear;
  }
  
  .cards__item__link:hover {
    box-shadow: 0 6px 20px rgba(34, 40, 49, 0.3);
    -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.03));
    filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.03));
  }

  .cards__item__link:hover .cards__item__image {
    transform: scale(1.1);
    box-shadow: inset 0 0 0 1000px rgba(34,40,49,0.0);
  }
  
  .cards__item__info {
    padding: 20px 30px 30px;
  }
  
  .cards__item__text {
    color: #252e48;
    font-size: 18px;
    line-height: 24px;
  }
  
  @media only screen and (min-width: 1200px) {
    .content__blog__container {
      width: 84%;
    }
  }
  
  @media only screen and (min-width: 961px) {
    .cards__items {
      display: flex;
    }
  }
  
