.backdrop-container {
    background-color: rgba(26, 30, 37, 0.6);
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1000;
}

.backdrop-wrap {
    position: fixed;
    display: flex;
    flex-direction: column;
    align-items: center;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1001;
}

.backdrop-item {
    background-color: #ffffff;
    width: 100%;
    /*border: 2px solid #eeeeee;*/
    box-shadow: 0 6px 20px rgba(0, 0, 0, 0.17);
    -webkit-filter: drop-shadow(0 6px 20px rgba(0, 0, 0, 0.017));
    filter: drop-shadow(0 6px 20px rgba(0, 0, 0, 0.017));
    /*border-radius: 10px;*/
    overflow: hidden;
    text-decoration: none;
}

.backdrop-item-pic-wrap {
    position: relative;
    width: 100%;
    overflow: hidden;
}

.backdrop-item-pic-wrap::after {
    content: attr(data-category);
    position: absolute;
    bottom: 0;
    margin-left: 10px;
    padding: 6px 8px;
    max-width: calc((100%) - 60px);
    font-size: 12px;
    font-weight: 700;
    color: #eeeeee;
    background-color: #d65a31;
    box-sizing: border-box;
}

.backdrop-image {
    position: relative;
    min-width: 10vw;
    min-height: 10vh;
    max-width: 90vw;
    max-height: 80vh;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: block;
    object-fit: cover;
    transition: all 0.2s linear;
}

.backdrop-item-info {
    padding: 20px 30px 30px;
    z-index: 1001;
}

.backdrop-item-text {
    color: #252e48;
    font-size: 18px;
    line-height: 24px;
}

.backdrop-close-icon {
    background-color: #1A1E25;
    border: 2px solid #eeeeee;
    border-radius: 15px;
    width: 30px;
    height: 30px;
    color: #eeeeee;
    position: absolute;
    top: 0;
    right: 0;
    margin-top: -10px;
    margin-right: -10px;
    cursor: pointer;
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    padding-bottom: 5px;
    text-transform: uppercase;
    font-family: Arial, Calibri, Helvetica, sans-serif;
    font-size: 14px;
    box-shadow: 0 6px 20px rgba(0, 0, 0, 0.17);
    -webkit-filter: drop-shadow(0 6px 20px rgba(0, 0, 0, 0.017));
    filter: drop-shadow(0 6px 20px rgba(0, 0, 0, 0.017));
}

.backdrop-close-icon:hover {
    background-color: #eeeeee;
    color: #1A1E25;
    font-weight: bold;
    box-shadow: 0 6px 20px rgba(34, 40, 49, 0.3);
    -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.03));
    filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.03));
}

/* navigation */
.backdrop-nav-container {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
}

.backdrop-nav-left, .backdrop-nav-right {
    background-color: rgba(0, 0, 0, 0.0);
    position: absolute;
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 50px;
    height: 100%;
    opacity: .3;
    cursor: pointer;
}

.backdrop-nav-left {
    left: 0;
}

.backdrop-nav-right {
    right: 0;
}

.backdrop-nav-left:hover, .backdrop-nav-right:hover {
    background-color: rgba(0, 0, 0, 0.1);
    opacity: 1;
}

.backdrop-nav-left-icon, .backdrop-nav-right-icon {
    background-repeat: no-repeat;
    background-position: center;
    background-size: 70%;
    position: absolute;
    width: 50px;
    height: 50px;
}

.backdrop-nav-left-icon {
    background-image: url('../images/left-arrow.png');
}

.backdrop-nav-right-icon {
    background-image: url('../images/right-arrow.png');
}
/* navigation */

@media screen and (max-width: 960px) {
    .backdrop-image {
        min-width: auto;
        min-height: auto;
    }
}